<template>
  <div class="flex flex-wrap mx-auto p-3 sm:p-8">
    <div
      class="w-full lg:w-1/4 xl:w-1/5 md:w-1/3 px-3 py-3"
      v-for="(item, index) in list_portofolio"
      :key="index"
    >
      <div class="rounded overflow-hidden shadow hover:shadow-lg border-box">
        <div class="relative h-64 sm:h-48">
          <div class="absolute flex items-center w-full h-full bg-gray-600">
            <div class="flex-1 text-center w-1">
              <div>
                <half-circle-spinner
                  class="inline-block"
                  :animation-duration="1000"
                  :size="30"
                  color="#ffff"
                />
              </div>
            </div>
            <img class="w-full h-full object-cover absolute" :src="vue_logo" :alt="item.name">
          </div>
        </div>
        <div class="text-sm sm:text-md mt-3 pr-3 pl-3">Project Name : {{ item.name }}</div>
        <div class="px-3">
          <a :href="`${base_url}${item.name}`">
            <button
              class="w-full bg-blue-500 hover:bg-blue-700 text-sm text-white py-2 rounded mt-3 mb-3 border-box"
            >Open project</button>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import global_mixin from "@/mixins/global";
import { HalfCircleSpinner } from "epic-spinners";
export default {
  mixins: [global_mixin],
  components: {
    HalfCircleSpinner
  },
  data() {
    return {
      list_portofolio: [],
      local_env: "prod"
    };
  },
  methods: {
    generate_sample_data: function() {
      for (let index = 0; index < 100; index++) {
        this.list_portofolio.push({
          name: "name" + index
        });
      }
    },
    get_folder_project: async function() {
      try {
        let setting = await this.get_app_setting(this.my_setting);
        let projects = await this.get_all_folder_project();
        let filtered_projects = this.filter_folder_project(projects, setting);
        return filtered_projects;
      } catch (error) {
        window.alert(error);
      }
    },
    get_all_folder_project: async function() {
      return (await fetch(this.url_home)).json();
    },
    filter_folder_project: function(projects, setting) {
      return projects.filter(
        item =>
          item.type == "dir" && !setting.excluded_folder.includes(item.name)
      );
    }
  },
  mounted() {
    if (this.local_env == "prod") {
      this.get_folder_project().then(data => {
        this.list_portofolio = data;
      });
    } else {
      this.generate_sample_data();
    }
  },
  computed: {
    ...mapState({
      url_home: "url_home_portofolio",
      base_url: "base_url",
      vue_logo: "vue_logo",
      my_setting: "my_setting"
    })
  }
};
</script>
